import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'

export default {

	data() {
		return {
			showAgreement: true
		}
	},

	computed: {
		...mapState('authed', [
			'tokens',
		])
	},

	methods: {
		...mapActions('authed',['getBindMobileAction']),
		// ...mapGetters('authed', [
		// 	'dontTokens',
		// ]),

		getPhoneNumber(e) {
			// console.log(e)
			const _this = this
			if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
				uni.showModal({
					title: '提示',
					showCancel: false,
					content: '未授权'
				})
			} else {
				uni.showModal({
					title: '提示',
					showCancel: false,
					content: '同意授权',
					success: function(res) {
						console.log(_this)
						_this.bindMobile(e.detail);
					}
				})
			}
		},

		bindMobile(value) {
			console.log(value)
			const appId = getApp().globalData.appId;
			const {
				tenant,
				user
			} = this.tokens;

			const params = {
				"mobile": '13691224349',
				"app_id": appId,
				"tenant_id": tenant.id,
				"source": "wx_app::" + value.encryptedData + ":" + value.iv,
				"wx_open_id": user.wx_open_id, //可选，如果传了，绑定到对应的微信号
			}
			
			this.getBindMobileAction(params)
		},

		phoneLogin() {
			uni.navigateTo({
				url: './PhoneNumLogin',
			});
		},
		gotoAgreement() {
			uni.navigateTo({
				url: '../webView/webView'
			});
		}
	}
}
